import { Component, OnInit } from '@angular/core';
import { Router } from '@angular/router';
import { ApiService } from 'src/app/shared/service/api.service';
import { LocalDataService } from 'src/app/shared/service/local-data.service';

export interface IStaff  {
  id: string;
  name: string;
  staffNumber: string;
  email: string;
  department: string;
  organization: string;
}

@Component({
  selector: 'app-staffs',
  templateUrl: './staffs.component.html',
  styleUrls: ['./staffs.component.scss']
})
export class StaffsComponent implements OnInit {

  public staffs: IStaff[] = [
    // {
    //   id: '1',
    //   name: 'Darius Bualan',
    //   staffNumber: '81254880',
    //   email: 'darius@pccw.com',
    //   department: 'Department 1',
    //   organization: 'Org 2'
    // },
    // {
    //   id: '2',
    //   name: 'Jener Sigua',
    //   staffNumber: '81254807',
    //   email: 'jenersigua@pccw.com',
    //   department: 'Department 2',
    //   organization: 'Org 1'
    // },
  ];

  constructor(
    private apiService: ApiService,
    private localDataService: LocalDataService,
    private router: Router
  ) { }

  ngOnInit(): void {
    this.staffs = this.localDataService.get('staffs') || [
      {
          id: '1',
          name: 'Darius Bualan',
          staffid: '81254880',
          email: 'darius@pccw.com',
      }
    ];
    this.localDataService.save('staffs', this.staffs);
    // const dept: any = this.localDataService.get('department');
    // this.apiService.list('staff/department/' + dept?.id || '24' ).subscribe( x => {
    //   this.staffs = x;
    // });
  }

  add(): void {
    this.router.navigate(['staffs', 'form']);
  }

  delete(id): void{
    const sure = confirm('Are you sure?');
    if (!sure) {
      return;
    }
    this.staffs = this.staffs.filter( x => x.id !== id);
    this.localDataService.save('staffs', this.staffs);
  }

  edit(staff): void{
    this.localDataService.save('editStaff', staff);
    this.router.navigate(['staffs', 'form']);
  }
}
