import { Component, OnDestroy, OnInit } from '@angular/core';
import { FormBuilder, FormGroup, Validators } from '@angular/forms';
import { Router } from '@angular/router';
import { LocalDataService } from 'src/app/shared/service/local-data.service';

@Component({
  selector: 'app-staff-form',
  templateUrl: './staff-form.component.html',
  styleUrls: ['./staff-form.component.scss']
})
export class StaffFormComponent implements OnInit, OnDestroy {

  staffForm: FormGroup;
  editStaff: any;

  constructor(
    private fb: FormBuilder,
    private router: Router,
    private localData: LocalDataService
  ) { }

  ngOnInit(): void {
    this.editStaff = this.localData.get('editStaff');
    this.buildForm();
  }

  ngOnDestroy(): void {
    this.localData.delete('editStaff');
  }

  buildForm(): void {
    this.staffForm = this.fb.group({
      staffid: ['', Validators.required],
      name: ['', Validators.required],
      email: ['', [Validators.required, Validators.email]]
    });

    if (this.editStaff) {
      this.staffForm.patchValue({
        name: this.editStaff.name,
        email: this.editStaff.email,
        staffid: this.editStaff.staffid,
      });
    }
  }

  submitForm(): void {
    this.staffForm.markAllAsTouched();
    if (this.staffForm.invalid) {
      alert('Fields Required');
      return;
    }
    let currentStaffs = this.localData.get('staffs') || [];

    if (!this.editStaff) {
      currentStaffs.push({
        id: new Date().getTime(),
        ...this.staffForm.value
      });
    } else {
      currentStaffs = currentStaffs.map( x => {
        if (x.id === this.editStaff.id) {
          return {
            ...x,
            ...this.staffForm.value
          };
        }
        return x;
      });
    }


    this.staffForm.reset();
    this.localData.save('staffs', currentStaffs);
    alert('Success');
    this.router.navigate(['staffs']);
  }

  cancel(): void {
    this.router.navigate(['staffs']);
  }
}
